@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4 h5, h6, p, ul {
  margin: 0;
}

.app *{font-family:"Inter", sans-serif;box-sizing:border-box;color:#333}.app .bg-light{background-color:#eef5fe}.app .bg-primary{background-color:#1977f3}.app .btn{background:none;border:none;padding:10px 15px;border-radius:3px}.app .btn-primary{background-color:#1977f3;color:white}.app .btn-link{cursor:pointer}.app .btn-link:hover{text-decoration:underline}.app header{display:flex;justify-content:space-around;padding:8px}.app .title-container__title{font-weight:lighter}.app .nav-menu{display:flex;align-items:center;position:relative}.app .hamburger{height:70%;display:flex;flex-direction:column;justify-content:space-evenly;cursor:pointer;padding:0}.app .hamburger__line{width:35px;border:2px solid #333;background-color:#333;position:relative;transition:0.2s ease-out}.app .hamburger__line:first-child{top:11px;opacity:0.5;visibility:hidden}.app .hamburger__line:last-child{top:-11px;opacity:0.5;visibility:hidden}.app .hamburger.collapsed .hamburger__line{top:0;opacity:1;visibility:visible}.app .nav-menu ul{padding:0;list-style:none;position:absolute;top:70px;background-color:#eef5fe;border-radius:3px;right:-16px;opacity:1;visibility:visible;transition:0.2s ease-out}.app .section-bg{text-align:center}.app .section-bg__img{width:90%;max-width:1600px}.app .section-about{max-width:1200px;padding:8px;margin:0 auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.app .section-about__headshot img{width:100%;max-width:300px;border-radius:50%}.app .section-about__text h2{margin:20px 0}.app .section-about__text p{margin-bottom:10px}.app footer{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:8px}.app footer *{color:white}@media (max-width: 499px){.app .hidden{visibility:hidden !important;opacity:0 !important}}@media (min-width: 500px){.app .hamburger{display:none}.app .nav-menu ul{position:relative;top:0;background-color:transparent;right:0;visibility:visible;opacity:1;display:flex}.app footer{flex-direction:row;justify-content:space-around}}@media (min-width: 960px){.app .section-about{display:grid;grid-column-gap:8px;-webkit-column-gap:8px;column-gap:8px;grid-template-columns:auto auto;align-items:start}.app .section-about__headshot{margin-top:20px}.app .section-about__headshot img{width:300px}}

